
.header {
	grid-area: header;
	background-color: #ffffff;
}
.wrapper {
	max-width: 960px;
	margin: 0 auto;
	width: 100%;
	display: flex;
	justify-content: space-between;
	height: 100%;
	align-items: center;
	.img {
		width: 160px;
		display: block;
		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
}
.topProfileLink {
	position: relative;
	height: 100%;
}
.headBlock {
	display: flex;
	align-items: center;
	justify-content: flex-end;
	cursor: pointer;
	position: relative;
	height: 100%;
	padding: 0 5px;
	&:hover {
		background: rgba(174, 183, 194, 0.12);
		.modal {
			opacity: 1;
			visibility: visible;
		}
	}
	&__active {
		display: flex;
		align-items: center;
		justify-content: flex-end;
		cursor: pointer;
		position: relative;
		height: 100%;
		padding: 0 5px;
		background: rgba(174, 183, 194, 0.12);
	}
}
.headName {
	margin-right: 10px;
	font-weight: 500;
	font-size: 14px;
}
.arrow {
	width: 10px;
	height: 10px;
	background: url('./topProfileLink/icons/arrow-down.svg') center / cover no-repeat;
}
.avatar {
	margin-right: 10px;
	width: 32px;
	height: 32px;
	border-radius: 50%;
	overflow: hidden;
	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}
.modal {
	background: #fff;
	border: 1px solid #dce1e6;
	padding: 4px 0;
	box-shadow: 0 1px 3px rgba($color: #fff, $alpha: 8%);
	margin-top: 5px;
	min-width: 206px;
    max-width: 230px;
	z-index: 10;
	position: absolute;
	top: 100%;
	right: 0;
}
.modalHead {
	display: flex;
	padding: 8px 15px 8px;
	text-decoration: none;
	align-items: center;
	&:hover {
		background: rgba(174, 183, 194, 0.12);
	}
}
.modalName {
	color: #2a5885;
	font-size: 13px;
}
.item {
	padding: 0 15px;
	cursor: pointer;
	height: 30px;
	line-height: 30px;
	&:hover {
		background: rgba(174, 183, 194, 0.12);
	}
}
.divider {
	margin: 4px 13px;
	border-top: 1px solid #dce1e6;
}
.login {
	border: none;
	border-radius: 5px;
	padding: 8px 13px;
	background: steelblue;
	color: #fff;
	float: right;
	cursor: pointer;
	outline: none;
	font-size: 12.5px;
	text-decoration: none;
	height: max-content;
	&:active {
		background: #4a76a8;
		padding-bottom: 7px;
		padding-top: 9px;
	}
}