
.sidebar {
	grid-area: nav;
	ul {
		padding: 0;
		li {
			list-style-type: none;
			a {
				padding: 6px 15px;
				cursor: pointer;
				display: block;
				text-decoration: none;
				border-radius: 4px;
				color: #000;
				&.active {
					font-weight: 600;
					background: #fff;
				}
			}
			&:hover {
				background-color: rgba($color: #d7d8da, $alpha: 1);
				border-radius: 4px;
			}
		}
	}
}