
@keyframes loading-pac-1 {
    0% { transform: rotate(0deg) }
   50% { transform: rotate(-45deg) }
  100% { transform: rotate(0deg) }
}
@keyframes loading-pac-2 {
    0% { transform: rotate(180deg) }
   50% { transform: rotate(225deg) }
  100% { transform: rotate(180deg) }
}
.loading {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 800px;
}
.loading-pac > div:nth-child(2) {
  transform: translate(-15px,0);
}
.loading-pac > div:nth-child(2) div {
  position: absolute;
  top: 40px;
  left: 40px;
  width: 120px;
  height: 60px;
  border-radius: 120px 120px 0 0;
  background: #f8b26a;
  animation: loading-pac-1 1s linear infinite;
  transform-origin: 60px 60px
}
.loading-pac > div:nth-child(2) div:nth-child(2) {
  animation: loading-pac-2 1s linear infinite
}
.loading-pac > div:nth-child(2) div:nth-child(3) {
  transform: rotate(-90deg);
  animation: none;
}@keyframes loading-pac-3 {
    0% { transform: translate(190px,0); opacity: 0 }
   20% { opacity: 1 }
  100% { transform: translate(70px,0); opacity: 1 }
}
.loading-pac > div:nth-child(1) {
  display: block;
}
.loading-pac > div:nth-child(1) div {
  position: absolute;
  top: 92px;
  left: -8px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #4682b4;
  animation: loading-pac-3 1s linear infinite
}
.loading-pac > div:nth-child(1) div:nth-child(1) { animation-delay: -0.67s }
.loading-pac > div:nth-child(1) div:nth-child(2) { animation-delay: -0.33s }
.loading-pac > div:nth-child(1) div:nth-child(3) { animation-delay: 0s }
.loading-item {
  width: 200px;
  height: 200px;
  display: inline-block;
  overflow: hidden;
  background: none;
}
.loading-pac {
  width: 100%;
  height: 100%;
  position: relative;
  transform: translateZ(0) scale(1);
  backface-visibility: hidden;
  transform-origin: 0 0; 
}
.loading-pac div { box-sizing: content-box; }
