body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #edeef0;
	box-sizing: border-box;
}
code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}
ul {
	margin: 0;
}
.wrapper { 
	display: grid;
	grid-template-areas:
	"header"
	"content";
	grid-template-rows: 48px;
	grid-template-columns: 1fr;
	grid-gap: 10px;
	margin: 0 auto;
	&__content {
		background-color: #ffffff;
		grid-template: 'main';
	}
	&__navbar {
		grid-template: nav;
	}
	main {
		display: grid;
		grid-template-areas: 
		'nav' 'main';
		grid-template-columns: 2fr 10fr;
		grid-gap: 10px;
		max-width: 960px;
		margin: 0 auto;
		grid-area: content;
		width: 100%;
	}
}

button {
	border: none;
	border-radius: 5px;
	padding: 8px 13px;
	background: steelblue;
	color: #fff;
	float: right;
	cursor: pointer;
	outline: none;
	font-size: 12.5px;
	&:active {
		background: #4a76a8;
		padding-bottom: 7px;
		padding-top: 9px;
	}
}
textarea,input {
	outline: none;
}